body {
   margin: 0;
}

.App {
  text-align: center;
  width: 100vw;
  background-size: cover;
  background-repeat: repeat;
}

.logo {
  height: 15vmin;
  border-radius: 50%;
}

nav {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content:space-around;
  align-items: center;
  min-height: 10vh;
  background-color: rgba(252, 252, 252, 0.603);
  border: none;
  font-size: 1.3rem;
}

.nav-link {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 15%;
  color: green;
  margin-right: 1.2rem;
}

.nav-link:hover {
  color: #3d2e25;
  border-left: solid 2px #3d2e25;
  border-right: solid 2px #3d2e25;

}

link {
  border-right: none;
}

.Home {
  display: flex;
  justify-content:center;
  align-items: center;
  flex-wrap: wrap;
}

.card {
  width: 20rem;
  height: 10rem;
  padding: 2rem;
  color: #3d2e25;
  font-size: 1.3rem;
  margin: 1rem;
}

.cardWrapper {
  display: flex;
  justify-content: center;
  margin: 1rem;
  flex-wrap: wrap;
}

h2 {
  color: green;
  font-size: 2rem;
}

.space {
  height: 10rem;
}

.aboutBox {
  align-items: center;
}

.aboutWrapper {
  background-color: rgba(247, 192, 192, 0.89);
  width: 50%;
  height: max-content;
  margin-left: auto;
  margin-right: auto;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-top: 0.5rem;
  margin-top: 1rem;
  padding-bottom: 1rem;
  color: #3d2e25;
  font-size: 1rem;
  
}

.Contact {
  align-items: center;
  background-color: rgba(255, 255, 255, 0.664);
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem;
  margin-top: 4rem;

}

.home-right {
  margin-left: 3rem;
}

li {
  list-style: none;
}

footer {
  height: 1rem;
  position: fixed;
  width: 100%;
  bottom: 0;
  background-color: #3d2e25;
  color: white;
  text-align: center;
}

.spacer {
  height: 15rem;
}

.homeHeader {
  color: green;
  font-size: 7em;
  font-weight: bold;
  margin-bottom: 0px;
}

.ceoPic {
  width:15rem;
  height:10rem;

}

.homeButton {
  color:green;
  background-color:transparent;
  border-radius:25px; 
  border:solid 2px green;
  height:4rem;
  width:8rem;
  font-size:2rem;
}

.contactHeader {
  color:green;
  font-size:3em;
   font-weight:bold; 
   margin-bottom:0
}

.contactList {
  color:#624a3c;
  font-size:2em;
  margin-top:0
}

.phoneNumberButton {
  color:green;
  background-color:transparent;
  border-radius:25px;
  border:solid 2px green;
  height:2rem;
  width:10rem; 
  font-size:1rem; 
  margin-top:3rem;
}

.greenLink {
  text-decoration:none;
  color:green;
}

.aboutHeader {
  color: #624a3c;
}

.boldText {
  font-weight: bold;
}

.slogan {
  font-size: 1.5rem;
  color:#624a3c;
}

.taco {
  width: 18rem;
  height: 12rem;
  padding-top: 1.2rem;
  margin-right: 4rem;
}

@media only screen and (max-width: 700px) {
  .homeHeader {
    font-size: 4rem;
  }

  .homeButton {
    font-size: 1rem;
    height:2rem;
    width:4rem;
  }

  .taco {
    width: 8rem;
    height: 4rem;
    padding-top: 3rem;
    margin-right: 4rem;
  }

  .slogan {
    font-size: 1rem;
    color:#624a3c;
    margin-top:0;
  }

  .aboutQuote {
    font-size: 0.5rem;
  }

  .contactHeader {
    font-size: 2rem;
  }

  .contactList {
    font-size: 1rem;
  }

  .phoneNumberButton {
    height:1.5rem;
    width:8rem; 
    font-size:1rem;
  }

  nav {
    font-size: 1rem;
  }
}

